import React, { useState, useEffect } from "react";
import "./OpeningParagraph.css";
import TextAndInput from "./TextAndInput";
import { Paragraph, TextRun, AlignmentType } from "docx";

function OpeningParagraph({ updateDoc }) {
  const [meterial, setMeterial] = useState("");
  const [locationType, setLocationType] = useState("");
  const [customLocationType, setCustomLocationType] = useState("");
  const [location, setLocation] = useState("");

  const [recipient, setRecipient] = useState("");
  const [date, setDate] = useState("");
  const [contactPerson, setContactPerson] = useState("");
  const [project, setProject] = useState("");

  const [surveyDate, setSurveyDate] = useState("");
  const [engineer, setEngineer] = useState("");
  const [opinion, setOpinion] = useState("");

  // Update doc elements whenever any of the form fields change
  useEffect(() => {
    const docElements = [
      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing
        children: [
          new TextRun({
            text: `: ${recipient || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
      new Paragraph({ text: "", spacing: { after: 200 } }), // Space after the first paragraph

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: `: ${date || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
      new Paragraph({ text: "", spacing: { after: 200 } }), // Space after the first paragraph

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: `: ${contactPerson || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
      new Paragraph({ text: "", spacing: { after: 200 } }), // Space after the first paragraph

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: `: ${project || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
      new Paragraph({ text: "", spacing: { after: 400 } }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: `: "     ${
              meterial || "_________"
            }`,
            font: "David",
            size: 32,
            bold: true,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: `: ${
              locationType === "" && customLocationType
                ? customLocationType
                : locationType || "_________"
            } ${location || "_________"}`,
            font: "David",
            size: 32,
            bold: true,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({ text: "", spacing: { after: 400 } }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 }, // 1.5 line spacing

        children: [
          new TextRun({
            text: ` ${surveyDate || "_________"}    - ${
              locationType === "" && customLocationType
                ? customLocationType
                : locationType || "_________"
            } " "  ${
              engineer || "_________"
            }      ${
              meterial || "_________"
            }  - ${
              locationType === "" && customLocationType
                ? customLocationType
                : locationType || "_________"
            } ${location || "_________"}      ${
              opinion || "_________"
            }            ( ,  /   )`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
    ];

    // Pass the generated doc elements to the parent
    updateDoc(docElements);
  }, [
    recipient,
    date,
    contactPerson,
    project,
    meterial,
    locationType,
    customLocationType,
    location,
    surveyDate,
    engineer,
    opinion,
    updateDoc,
  ]);

  return (
    <>
      <div className="initial-info-container">
        <div className="half-cont">
          <TextAndInput
            label=":"
            type="date"
            value={date}
            setValue={setDate}
          />
          <TextAndInput
            label=":"
            value={project}
            setValue={setProject}
          />
        </div>
        <div className="half-cont">
          <TextAndInput
            label=":"
            value={recipient}
            setValue={setRecipient}
          />
          <TextAndInput
            label=":"
            value={contactPerson}
            setValue={setContactPerson}
          />
        </div>
      </div>
      <div className="text-paragraph-top">
        <div className="form-group">
          <label htmlFor="meterials" className="form-label">
            : "    
          </label>
          <select
            name="meterials"
            id="meterials"
            value={meterial}
            onChange={(e) => setMeterial(e.target.value)}
            className="form-select"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=" "> </option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="locations" className="form-label">
            :
          </label>
          <select
            name="locations"
            id="locations"
            value={locationType}
            onChange={(e) => {
              setLocationType(e.target.value);
              if (e.target.value !== "") setCustomLocationType("");
            }}
            className="form-select"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          {locationType === "" && (
            <input
              type="text"
              className="input-field"
              placeholder="  "
              value={customLocationType}
              onChange={(e) => setCustomLocationType(e.target.value)}
            />
          )}

          <input
            type="text"
            className="input-field"
            placeholder=" "
            value={location}
            onChange={(e) => setLocation(e.target.value)}
          />
        </div>
      </div>
      <div className="text-paragraph-bottom">
        <p>
          {" "}
          <TextAndInput
            type="date"
            value={surveyDate}
            setValue={setSurveyDate}
          />{" "}
             -{" "}
          {locationType === "" && customLocationType
            ? customLocationType
            : locationType || "_________"}{" "}
          " " {" "}
          <TextAndInput value={engineer} setValue={setEngineer} />.
        </p>
        <p>
               ' {meterial || "_________"}  -{" "}
          {locationType === "" && customLocationType
            ? customLocationType
            : locationType || "_________"}{" "}
          {location || "_________"}     {" "}
          <TextAndInput value={opinion} setValue={setOpinion} />.
        </p>
        <p>
                    
          ( ,  /   ').
        </p>
      </div>
    </>
  );
}

export default OpeningParagraph;
