import React, { useContext } from "react";
import { TableContext } from "./TableContextProvider";
import "./DataTable.css";

const DataTable = () => {
  const { table, handleInputChange, addRow, addDeficiency, addRecommendation } =
    useContext(TableContext);

  const handleImageUpload = (index, file) => {
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        handleInputChange(index, "image", reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="table-wrapper">
      <div className="table-container">
        <table className="styled-table">
          <thead>
            <tr>
              <th>"</th>
              <th></th>
              <th></th>
              <th> </th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {table.map((row, index) => (
              <tr key={index}>
                <td>{row.index}</td>
                <td>
                  <select
                    name="materials"
                    value={row.material}
                    onChange={(e) =>
                      handleInputChange(index, "material", e.target.value)
                    }
                    className="form-select"
                  >
                    <option value="">  </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                  {row.material === "" && (
                    <input
                      type="text"
                      className="input-field table-other-input"
                      value={row.materialOther || ""}
                      onChange={(e) =>
                        handleInputChange(
                          index,
                          "materialOther",
                          e.target.value
                        )
                      }
                      placeholder="  "
                    />
                  )}
                </td>
                <td>
                  <select
                    name="elements"
                    value={row.element}
                    onChange={(e) =>
                      handleInputChange(index, "element", e.target.value)
                    }
                    className="form-select"
                  >
                    <option value="">  </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                  {row.element === "" && (
                    <input
                      type="text"
                      className="input-field table-other-input"
                      value={row.elementOther || ""}
                      onChange={(e) =>
                        handleInputChange(index, "elementOther", e.target.value)
                      }
                      placeholder="  "
                    />
                  )}
                </td>
                <td>
                  {row.deficiencies.map((deficiency, dIndex) => (
                    <div key={dIndex} className="deficiency-row">
                      <select
                        value={deficiency.value || ""}
                        onChange={(e) =>
                          handleInputChange(
                            index,
                            "deficiencies",
                            e.target.value,
                            dIndex,
                            null
                          )
                        }
                        className="form-select"
                      >
                        <option value=""> </option>
                        <option value=""> </option>
                        <option value=" "> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value="    ">
                              
                        </option>
                        <option value="  ">
                            
                        </option>
                        <option value="  ">  </option>
                        <option value="   ">
                             
                        </option>
                        <option value=""></option>
                      </select>
                      {deficiency.value === "" && (
                        <input
                          type="text"
                          className="input-field table-other-input"
                          value={deficiency.customValue || ""}
                          onChange={(e) =>
                            handleInputChange(
                              index,
                              "deficiencyOther",
                              e.target.value,
                              dIndex,
                              null
                            )
                          }
                          placeholder="  "
                        />
                      )}
                    </div>
                  ))}
                  <button
                    onClick={() => addDeficiency(index)}
                    className="add-deficiency"
                  >
                    +
                  </button>
                </td>
                <td>
                  {row.recommendations.map((recommendation, rIndex) => (
                    <div key={rIndex} className="recommendation-row">
                      <select
                        value={recommendation.value || ""}
                        onChange={(e) =>
                          handleInputChange(
                            index,
                            "recommendations",
                            e.target.value,
                            null,
                            // i had to send null there because this is the dIndex and i need to match the handleInputChange parameters
                            rIndex
                          )
                        }
                        className="form-select"
                      >
                        <option value=""> </option>
                        <option value=""></option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value="  ">
                            
                        </option>
                        <option value=" "> </option>
                        <option value="   ">
                             
                        </option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value=" "> </option>
                        <option value=""></option>
                      </select>
                      {recommendation.value === "" && (
                        <input
                          type="text"
                          className="input-field table-other-input"
                          value={recommendation.customValue || ""}
                          onChange={(e) =>
                            handleInputChange(
                              index,
                              "recommendationOther",
                              e.target.value,
                              null,
                              rIndex
                            )
                          }
                          placeholder="  "
                        />
                      )}
                    </div>
                  ))}
                  <button
                    onClick={() => addRecommendation(index)}
                    className="add-recommendation"
                  >
                    +
                  </button>
                </td>
                <td>
                  <select
                    name="severities"
                    id="severities"
                    value={row.severity}
                    onChange={(e) =>
                      handleInputChange(index, "severity", e.target.value)
                    }
                    className="form-select"
                  >
                    <option value="">  </option>
                    <option value="1">1 -   </option>
                    <option value="2">2 - </option>
                    <option value="3">3 -  </option>
                  </select>
                </td>
                <td>
                  <label className="image-input-label">
                    <input
                      className="image-input"
                      type="file"
                      accept="image/*"
                      onChange={(e) =>
                        handleImageUpload(index, e.target.files[0])
                      }
                    />
                  </label>
                  {row.image && (
                    <img
                      src={row.image}
                      alt="Preview"
                      style={{
                        width: "150px",
                        height: "150px",
                        margin: "10px",
                        objectFit: "cover",
                        objectPosition: "center center",
                      }}
                    />
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="add-row-container">
        <button onClick={addRow} className="add-row">
          +
        </button>
      </div>
    </div>
  );
};

export default DataTable;
